.loader.hide {
  opacity: 0;
  pointer-events: none;
}

.loader {
  width: 160px;
  height: 185px;
  background: #5b5353;
  border-radius: 100px 100px 0 0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after {
  content: "";
  width: 100px;
  height: 125px;
  background-image: radial-gradient(circle, #000 48%, rgba(0, 0, 0, 0) 55%), radial-gradient(circle, #000 48%, rgba(0, 0, 0, 0) 55%), radial-gradient(circle, #5b5353 30%, rgba(0, 0, 0, 0) 45%), radial-gradient(circle, #000 48%, rgba(0, 0, 0, 0) 51%), linear-gradient(#000 20px, rgba(0, 0, 0, 0) 0), linear-gradient(#5b5353 60px, rgba(0, 0, 0, 0) 0), radial-gradient(circle, #5b5353 50%, rgba(0, 0, 0, 0) 51%), radial-gradient(circle, #5b5353 50%, rgba(0, 0, 0, 0) 51%);
  background-position: 25px 10px, 55px 10px, 36px 44px, 50% 30px, 50% 85px, 50% 50px, 50% 22px, 50% 45px;
  background-repeat: no-repeat;
  background-size: 16px 16px, 16px 16px, 10px 10px, 42px 42px, 12px 3px, 50px 25px, 70px 70px, 70px 70px;
  animation: faceLift 3s linear infinite alternate;
  position: absolute;
  top: 25px;
  left: 50%;
  transform: translateX(-50%);
}

.loader:before {
  content: "";
  width: 140%;
  height: 125px;
  background-image: radial-gradient(circle, #5b5353 48%, rgba(0, 0, 0, 0) 50%), radial-gradient(circle, #5b5353 48%, rgba(0, 0, 0, 0) 50%);
  background-position: 0 12px, 145px 12px;
  background-repeat: no-repeat;
  background-size: 65px 65px;
  animation: earLift 3s linear infinite alternate;
  position: absolute;
  top: 0;
  left: -20%;
}

@keyframes faceLift {
  0% {
    transform: translateX(-60%);
  }

  100% {
    transform: translateX(-30%);
  }
}

@keyframes earLift {
  0% {
    transform: translateX(10px);
  }

  100% {
    transform: translateX(0);
  }
}

